    <marquee class="mt-3 danger" style="color:#4caf50" direction="left">
      <h5>慶祝開台~~輸入優惠碼666,全館八折</h5>
    </marquee>
    <div class="container">
      <div class="row mt-2">
        <div class="col-md-3">
          <!-- 左側選單 (List group) -->
          <div class="list-group sticky-top" style="border-radius:15px">
            <a
              href="#"
